.row_posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 10px;
    
}
/* .row_posters::-webkit-scrollbar{
    display: none;
} */

.row_poster {
    width: 100%;
    object-fit: contain;
    max-height: 100px;
    margin-right: 10px;
    transition: transform 450ms;
}
.row_poster:hover{
    transform: scale(1.08);
    opacity: 1;
}
.row{
    margin-right: 5px;
    
}